*{
    margin:0;
    padding:0;
}
body{
    background-color: black;
    color: aliceblue;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
nav{
    overflow: hidden;
  
    margin: 10px;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}
.title-history{
    padding: 30px;
    position:absolute;
}
.navigation-button-back{
    border: solid 1px white;
    margin: 20px 500px;
    padding-left:80px;
    padding-right: 80px;
    padding-bottom: 10px;
    padding-top: 10px;
    position:absolute;
    text-align: center;
    font-size: 17px;
transition: all 0.5s;
}
.navigation-button-back:hover{
    padding-left:70px;
    padding-right: 70px;
    padding-bottom: 15px;
    padding-top: 15px;
    background-color: rgb(98, 96, 96);
    color:aliceblue;
    font-size: 20px;
}
/* astea trebe sa le fac ca sa nu mai ma chinui  */
